@-webkit-keyframes spin{
    0%{-webkit-transform: none;transform: none;}
    50%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);} 
 }    
 


 
@keyframes spin{
    0%{-webkit-transform: none;transform: none;}
    50%{-webkit-transform: rotate(0deg);transform: rotate(0deg);}
    100%{-webkit-transform: rotate(360deg);transform: rotate(360deg);} 
 }

 @-webkit-keyframes title{
    0%{opacity:0;}
    50%{opacity: 50;}
    100%{opacity: 100;} 
 }

