.body-rights{
    height: 100%;
    width: 100%;
    margin-right: 55px;

}

.containersy{
    height:102%;
    width: 60%;
    margin: -5.5rem auto;
    box-shadow: 0rem 0rem 2rem 0.1rem #48ea98;
    border-radius: 0.5rem;
    padding-top: 1rem;

 
}

.cvb{
    font-weight: 1000;
    margin-right: 7rem;
    font-size: 1.2rem;
    text-align: center;
}

.inner-group{
    margin-left: 6rem;
    width: 110%;
    position: relative;
}
.loki{
    margin-left: 6rem;
    width: 110%;
    position: relative;
    font-size: 12px;
}

.hac{
    font-size: small;
    font-weight: 300;
    color: gray;
    left: 20%;
    top: 0rem;
    position: absolute;
    margin: 0.5rem auto;
    background: white;
}

.submay{
    width: 66%;
    background: #26343D;
    color: white;
    cursor: pointer;
    border: none;
    padding: 0.5rem 2.5rem;
    transition:all 0.3s ease-in-out;
    margin-left: 6rem;
    margin-top: 10px;
}

.submay:hover{
    box-shadow: 0.5rem 0.5rem 1rem gray;
}

#sbtn:hover{
    box-shadow: 0.5rem 0.5rem 1rem gray;
}

.inner-group input{
   
    box-shadow: 0.1rem 0.1rem 0.3rem 0.1rem gray;
}

.loki input{
    box-shadow: 0.1rem 0.1rem 0.3rem 0.1rem gray;
}

.inputs{
    margin: 1.2rem auto;
    width: 60%;
    border: none;
    padding: 0.5rem 0.5rem;
}



.inputs:focus{
    outline: none;
}

.ds-content h1{
    font-weight: bold;
    cursor: pointer;
    color: #26343D;
}

.ds-content .d-submit{
    background-color: #151E73;
    border: none;
    padding-left:6px;
}     

