.widget{
    display: flex;
    width: 250px;
    justify-content: space-between;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    border-radius: 10px;
    height: 100px;
 }

 .left , .right{
display: flex;
flex-direction: column;
justify-content: space-between; 
 }

 .icon{
   padding : 2px;
   border-radius: 5px ;
   align-self: flex-end;
 }


 .title{
  margin: 0px;
   color:gray;
 }